.contact-page {
  padding: 2rem 0;
}

.contact-info {
  background: #f8f9fa;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.contact-info h3 {
  margin-bottom: 1.5rem;
  color: #007bff;
}

.contact-info ul {
  line-height: 1.8;
  color: #495057;
}

.contact-info a {
  color: #007bff;
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}

.map-container {
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

form {
  background: #ffffff;
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  margin-top: 2rem;
}

.form-row {
  margin-bottom: 1rem;
}

.form-group label {
  font-weight: 600;
  color: #495057;
  margin-bottom: 0.5rem;
}

.form-control {
  border-radius: 8px;
  border: 1px solid #ced4da;
  box-shadow: none;
  transition: border-color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.form-control:focus {
  border-color: #007bff;
  box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.25);
}

.btn-primary {
  background-color: #007bff;
  border-color: #007bff;
  padding: 0.75rem 1.25rem;
  font-size: 1.125rem;
  border-radius: 8px;
  transition: background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}

.btn-primary:hover {
  background-color: #0056b3;
  border-color: #004085;
}

.btn-block {
  width: 100%;
}

@media (max-width: 768px) {
  .map-container iframe {
      height: 250px;
  }
}

@media (max-width: 576px) {
  .contact-info {
      padding: 1.5rem;
  }

  form {
      padding: 1.5rem;
  }
}
