/* Footer.css */

.footer {
  background-color: #343a40;
  color: #f8f9fa;
}

.footer-heading {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.footer p {
  font-size: 0.875rem;
  line-height: 1.5;
}

.footer a {
  color: #ffffff;
  text-decoration: none;
}

.footer a:hover,
.footer a:focus {
  color: #b0bec5; /* Light Gray on hover */
  text-decoration: underline;
}

.social-links {
  margin-top: 1rem;
}

.social-links a {
  font-size: 1.5rem;
  margin-right: 0.5rem;
  transition: color 0.3s ease-in-out;
}

.social-links a:hover {
  color: #b0bec5; /* Light Gray on hover */
}

.footer .small {
  font-size: 0.875rem;
  line-height: 1.5;
}

.footer .text-center {
  text-align: center;
}

.footer .container {
  max-width: 1140px;
}

@media (max-width: 767.98px) {
  .footer .col-md-4,
  .footer .col-md-3,
  .footer .col-md-5 {
    text-align: center;
  }

  .footer .social-links {
    text-align: center;
  }
}

@media (min-width: 768px) {
  .footer .social-links a {
    margin-right: 1rem;
  }
}
