/* General header styles */
.header {
  background-color: #007bff; /* Primary color */
  padding: 1rem 0; /* Use rem units for better scalability */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header-title {
  color: #fff;
  text-align: center; /* Ensures title is centered */
  margin-bottom: 1rem;
}

.header-title h1 {
  font-size: 2.5rem; /* Larger font size */
  font-weight: bold;
}

.header-title p {
  font-size: 1.2rem;
  font-weight: 300;
}

/* Navbar styles */
.navbar {
  padding: 0;
  position: relative; /* Ensure navbar is positioned correctly */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.navbar-toggler {
  border: none;
  color: #fff;
  font-size: 1.5rem;
  background: transparent; /* Remove background */
  cursor: pointer; /* Add cursor pointer for better UX */
}

.navbar-collapse {
  display: none; /* Hide by default */
  flex-direction: column;
  align-items: center;
}

.navbar-collapse.show {
  display: flex; /* Show when toggled */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.navbar-nav {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 20px; /* Add gap between nav items in desktop view */
}

.nav-item {
  margin: 1rem 0; /* Vertical space between items */
}

.nav-link {
  color: #fff;
  padding: 0.5rem 1rem; /* Adjust padding for better responsiveness */
  font-size: 1.1rem; /* Slightly larger font size for better readability */
  text-transform: uppercase;
  font-weight: 500;
  transition: color 0.3s, background-color 0.3s;
}

.nav-link:hover {
  color: #007bff;
  background-color: #fff;
  border-radius: 5px;
}

/* Donate button */
.btn-donate {
  font-weight: bold;
  padding: 0.5rem 1rem; /* Adjust padding for better responsiveness */
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.btn-donate:hover {
  background-color: #e0a800;
  color: #fff;
}

/* Mobile view */
@media (max-width: 576px) {
  .navbar-nav {
    flex-direction: column;
    align-items: center;
  }

  .nav-item {
    margin-bottom: 1rem; /* Spacing for mobile */
  }

  .navbar-collapse {
    display: none; /* Hide by default */
  }

  .navbar-collapse.show {
    display: block; /* Show when toggled */
  }

  .nav-link {
    padding: 0.5rem 1rem; /* Adjust padding for better responsiveness */
  }

  .navbar-toggler {
    display: block; /* Show hamburger icon in mobile view */
  }
}

/* Desktop view */
@media (min-width: 577px) {
  .navbar-nav {
    flex-direction: row;
    justify-content: space-between;
  }

  .nav-item {
    margin: 0 1rem; /* Horizontal space between items */
  }

  .navbar-collapse {
    display: flex; /* Show by default */
  }

  .navbar-toggler {
    display: none; /* Hide hamburger icon in desktop view */
  }
}