/* Hero Section Slideshow */
.hero-section {
  margin-top: 2px;
  position: relative;
  height: 70vh; /* Full viewport height */
  overflow: hidden;
}

.slideshow-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  animation: slideBackground 15s infinite;
}

.slideshow-background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* Overlay for better text readability */
  z-index: 1;
}

@keyframes slideBackground {
  0% { background-image: url('../images/hero1.jpg'); }
  25% { background-image: url('../images/hero2.jpg'); }
  50% { background-image: url('../images/hero3.jpg'); }
  75% { background-image: url('../images/hero4.JPG'); }
  100% { background-image: url('../images/hero1.jpg'); }
}

.hero-content {
  position: relative;
  z-index: 2; /* Ensure content is above the background */
  text-align: center;
  color: #fff;
  padding: 20px;
  margin-top: 5%;
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.hero-content p {
  font-size: 1.25rem;
  margin-bottom: 30px;
}

.learn-more-btn {
  font-size: 1.25rem;
}

/* About Section */
.about-section {
  position: relative;
  padding: 60px 0;
}

.about-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

/* Mission Section */
.mission-section {
  background: #f8f9fa;
  padding: 60px 0;
}

.mission-card {
  border: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Call-to-Action Section */
.cta-section {
  background: #007bff;
  color: #fff;
  padding: 60px 0;
  text-align: center;
}

.cta-section h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.cta-section p {
  font-size: 1.25rem;
  margin-bottom: 30px;
}


/* Responsive Design */
@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 2rem;
  }

  .hero-content p {
    font-size: 1rem;
  }

  .cta-section h2 {
    font-size: 2rem;
  }

  .cta-section p {
    font-size: 1rem;
  }
}
