.blog-page {
    font-family: 'Roboto', sans-serif;
    padding: 2rem;
  }
  
  .blog-posts {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
  }
  
  .blog-card {
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
    width: calc(33.333% - 1rem);
    transition: transform 0.2s;
  }
  
  .blog-card:hover {
    transform: scale(1.05);
  }
  
  
  
  .btn-read-more {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .btn-read-more:hover {
    background-color: #0056b3;
  }
  
  .blog-detail {
    background-color: #f8f9fa;
    border-radius: 8px;
    padding: 2rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .btn-go-back {
    background-color: #6c757d;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .btn-go-back:hover {
    background-color: #5a6268;
  }
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .blog-card {
      width: 100%;
    }
  }
  