/* General Styles */
h2 {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  color: #343a40;
  margin-bottom: 2rem;
}

.container {
  padding-top: 40px;
}

/* Search Bar Styling */
.search-bar {
  border-radius: 20px;
  padding: 10px 20px;
  font-size: 1.1rem;
  border: 1px solid #ccc;
  transition: border-color 0.3s ease;
}

.search-bar:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Book Card Styles */
.book-card {
  border: none;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.book-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
}

.book-cover {
  border-radius: 10px 10px 0 0;
  height: 240px;
  object-fit: cover;
}

/* Download Button */
.btn-download {
  background-color: #007bff;
  border: none;
  color: #fff;
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 5px;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: background-color 0.3s ease;
}

.btn-download:hover {
  background-color: #0056b3;
}

/* Responsive Media Queries */
@media (max-width: 1200px) {
  .book-cover {
    height: 220px;
  }
}

@media (max-width: 992px) {
  .book-cover {
    height: 200px;
  }
}

@media (max-width: 768px) {
  .book-cover {
    height: 180px;
  }
}

@media (max-width: 576px) {
  .book-cover {
    height: 160px;
  }
}
