/* ProjectsPage.css */

/* General Styles */
.projects-page {
  padding: 20px;
  background-color: #f9f9f9;
}

/* Card Styles */
.project-card {
  border: 1px solid #ddd;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  background-color: #fff;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
}

/* Image Styling */
.project-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

/* Card Content */
.project-card .card-body {
  padding: 15px;
  text-align: center;
}

/* Project Title */
.project-card .card-title {
  font-size: 1.25rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 10px;
}

/* Project Description */
.project-card .card-text {
  font-size: 1rem;
  color: #555;
  margin-bottom: 15px;
  line-height: 1.5;
}

/* Button */
.project-card .btn {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  font-size: 1rem;
  border-radius: 25px;
  transition: background-color 0.3s ease;
}

.project-card .btn:hover {
  background-color: #0056b3;
  color: #fff;
}

/* Headings */
h2 {
  font-family: 'Arial', sans-serif;
  color: #007bff;
  font-weight: bold;
  margin-bottom: 20px;
}

/* Responsive Grid */
.row {
  display: flex;
  flex-wrap: wrap;
}

.col-md-4 {
  flex: 0 0 33.3333%;
  max-width: 33.3333%;
  padding: 15px;
}

@media (max-width: 768px) {
  .col-md-4 {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media (max-width: 576px) {
  .col-md-4 {
    flex: 0 0 100%;
    max-width: 100%;
  }
}
