.events-page {
    padding: 20px;
  }
  
  .events-page h2 {
    color: #007bff; /* Primary color for headings */
    margin-bottom: 30px;
  }
  
  .event-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 20px;
  }
  
  .event-card {
    background-color: #ffffff;
    border: 1px solid #e1e1e1;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  }
  
  .event-card-header {
    border-bottom: 2px solid #007bff;
    margin-bottom: 15px;
  }
  
  .event-title {
    font-size: 18px;
    font-weight: bold;
    color: #333;
    margin: 0;
  }
  
  .event-date {
    font-size: 14px;
    color: #888;
    margin: 0;
  }
  
  .event-description {
    font-size: 14px;
    color: #555;
  }
  
  .event-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  }
  
  .past-event-card {
    border-color: #dcdcdc;
  }
  
  @media (max-width: 768px) {
    .event-card {
      padding: 15px;
    }
  
    .event-title {
      font-size: 16px;
    }
  
    .event-date {
      font-size: 12px;
    }
  
    .event-description {
      font-size: 12px;
    }
  }

  .video-section {
    margin: 40px 0;
    text-align: center;
  }
  
  .video-section iframe {
    max-width: 100%;
    
    border-radius: 8px;
  }
  
  