/* AboutPage.css */

/* General Styles */
.about-page {
  font-family: 'Arial', sans-serif;
  color: #333;
  padding: 30px 15px;
  background-color: #f9f9f9; /* Light background for contrast */
}

.about-page h2,
.about-page h3 {
  color: #007bff; /* Primary color for headings */
  font-weight: 700;
  margin-bottom: 20px;
}

.about-page p {
  font-size: 16px;
  line-height: 1.8;
  margin-bottom: 15px;
}

.about-page a {
  color: #007bff;
  text-decoration: none;
}

.about-page a:hover {
  text-decoration: underline;
}

.about-page img {
  max-width: 100%;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Container and Row */
.container {
  max-width: 1200px;
  margin: 0 auto;
}

/* Responsive Layout */
@media (max-width: 768px) {
  .row {
    flex-direction: column;
  }
}

/* Card Styles */
.card {
  border: none;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  margin-bottom: 20px;
  transition: box-shadow 0.3s ease;
}

.card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
}

.card-body {
  padding: 20px;
}

.card-title {
  font-size: 20px;
  margin-bottom: 15px;
}

/* Mission and Contact Cards */
.mission-card {
  background-color: #e9ecef; /* Light background for distinction */
}

.contact-card {
  background-color: #ffffff;
}

.card-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.card-list li {
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 1.6;
}

/* History Section */
.history-section {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.history-timeline {
  list-style: none;
  padding: 0;
  margin: 0;
}

.history-timeline li {
  font-size: 16px;
  margin-bottom: 10px;
}

/* Testimonials Section */
.testimonials-section {
  background-color: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
}

.testimonials blockquote {
  border-left: 4px solid #007bff;
  padding-left: 20px;
  margin: 0;
  font-size: 18px;
  font-style: italic;
}

.testimonials footer {
  font-size: 16px;
  color: #555;
}

/* Impact Section */
.impact-section {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.impact-stats {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.stat {
  flex: 1;
  text-align: center;
  padding: 10px;
  border-right: 1px solid #dee2e6;
}

.stat:last-child {
  border-right: none;
}

.stat h4 {
  font-size: 24px;
  color: #007bff;
  margin-bottom: 5px;
}

.stat p {
  font-size: 16px;
  margin: 0;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .impact-stats {
    flex-direction: column;
  }

  .stat {
    border-right: none;
    border-bottom: 1px solid #dee2e6;
  }

  .stat:last-child {
    border-bottom: none;
  }
}
